<template>
  <div class="login-box">
    <el-row :gutter="10">
      <el-col :span="24" :md="{span: 12, offset: 6}" :lg="{span: 8, offset: 8}">
        <div class="login-box-logo">
          <el-image :src="url"></el-image>
        </div>
        <el-tabs v-model="activeName" @tab-click="handleClick" stretch="true">
          <el-tab-pane label="LOGIN" name="first">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" :label-width="labelWidth" class="login-box-form">
              <el-form-item label prop="username">
                <el-input type="text" v-model="ruleForm.username" placeholder="username" prefix-icon="el-icon-user" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label prop="password">
                <el-input type="password" v-model="ruleForm.password" placeholder="password" prefix-icon="el-icon-goods" autocomplete="off"></el-input>
              </el-form-item>
              <el-col :span="18">
                <el-form-item label prop="date1">
                  <el-input type="password" v-model="ruleForm.password" placeholder="password" prefix-icon="el-icon-refresh" autocomplete="off"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="{span: 4, offset: 2}">
                <el-image :src="code"></el-image>
              </el-col>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')" class="login-box-submit">Login</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="REGISTER" name="second">
            <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" :label-width="labelWidth" class="login-box-form">
              <el-form-item label prop="phone">
                <el-input type="text" v-model="ruleForm2.phone" placeholder="phone" prefix-icon="el-icon-phone-outline" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label prop="username">
                <el-input type="text" v-model="ruleForm2.username" placeholder="username" prefix-icon="el-icon-user" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item label prop="password">
                <el-input type="password" v-model="ruleForm2.password" placeholder="password" prefix-icon="el-icon-goods" autocomplete="off"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm2')" class="login-box-submit">Register</el-button>
              </el-form-item>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </el-col>
    </el-row>
    <div class="login-box-footer">Copyright © 2020 華為國際 出品</div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        url: 'http://team-power.hk/wp-content/uploads/2019/04/logo-1.png',
        activeName: 'first',
        labelWidth: '0',
        ruleForm: {},
        rules: {
          username: [
            { required: true, message: "please enter username", trigger: 'blur' }
          ],
          password: [
            { required: true, message: "please enter password", trigger: 'blur' }
          ]
        },
        code: 'data:image/jpg;base64,',
        ruleForm2: {},
        rules2: {
          phone: [
            { required: true, message: "please enter phone", trigger: 'blur' }
          ],
          username: [
            { required: true, message: "please enter username", trigger: 'blur' }
          ],
          password: [
            { required: true, message: "please enter password", trigger: 'blur' }
          ]
        },
      };
    },
    methods: {
      handleClick() {

      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    }
  }
</script>

<style lang="scss" scoped>
.login-box {
  margin-top: 100px;
  background: url('../assets/background.svg');
  background-size: 100%;
  &-logo {
    object-fit: contain;
    width: 100px; 
    height: 100px;
    margin: 0 auto;
  }
  &-form {
    margin-top: 10px;
  }
  &-submit {
    width: 100%;
  }
  &-footer {
    width: 100%;
    position: absolute;
    bottom: 30px;
    text-align: center;
    color: #999;
  }
}

@media screen and (min-width: 1200px) {
  .login-box-footer {
    width: 1400px;
    margin: 0 auto;
  }
}
</style>